body {
  box-sizing: border-box;
  margin: 0px;
}

ul li {
  list-style-type: none;
}

.newCoinFormButtons {
  .bigButton {
    display: block;
    text-align: center;
    text-decoration: none;
    font-size: 20px;
    font-weight: 400;
    width: 300px;
    border-radius: 30px;
    border: 0px;
    margin: 0px auto;

    &:hover {
      box-shadow: 2px 2px 3px rgb(66, 3, 124);
    }
  }

  .selectedBtn {
    background: -webkit-linear-gradient(to right, #f3a73c, hwb(288 30% 11%));
    background: linear-gradient(to right, #f3a73c, hwb(288 30% 11%));
    color: white;
    border: none;
    cursor: pointer;
    margin-bottom: 20px;
    padding: 18.5px 0px;

    &:hover {
      box-shadow: 2px 2px 3px rgb(66, 3, 124);
    }
  }

  .notSelectedBtn {
    color: rgb(92, 38, 187);
    background: none;
    background-color: white;
    font-size: 20px;
    font-weight: 400;
    border: none;
    cursor: pointer;
    padding: 15px 0px;

    &:hover {
      box-shadow: 2px 2px 3px rgb(66, 3, 124);
    }
  }

  .notSelectedBtn {
    color: rgb(92, 38, 187);
    background: none;
    background-color: white;
    font-size: 20px;
    font-weight: 400;
    border: none;
    cursor: pointer;

    &:hover {
      box-shadow: 2px 2px 3px rgb(66, 3, 124);
    }
  }
}

//Login/Register
.logWrapper,
.regWrapper,
.logoutWrapper {
  box-sizing: border-box;
  background: radial-gradient(
    circle,
    rgb(124, 69, 219) 30%,
    rgb(75, 40, 168) 66%,
    rgb(36, 18, 87) 100%
  );
  width: 100vw;
  margin: 0px;
}

.logWrapper {
  height: 100vh;
  padding-top: calc((100vh - 378px - 100px) / 2);
}

.regWrapper {
  height: 100vh;
  padding-top: calc((100vh - 520px) / 2);
}

.loginContainer {
  padding: 0px;
  width: inherit;
}

.registerContainer {
  padding: 0px;

  width: inherit;
}

.loginContainer,
.registerContainer {
  .logoWrapper {
    margin: 0px auto;
    color: white;
    font-size: 24px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    img {
      width: 30px;
      height: 30px;
      margin: 0px auto;
    }
  }

  .formLogReg {
    margin: 20px auto 0px auto;
    display: flex;
    flex-direction: column;
    gap: 30px;
    align-items: center;

    .formInput {
      display: flex;
      align-items: center;
      color: rgba(206, 204, 204, 0.664);
      font-size: 20px;
      font-weight: 400;
      border-bottom: 1px solid hsla(0, 2%, 80%, 0.664);
