      }

      input {
        background-color: inherit;
        margin-left: 3px;
        font-size: 20px;
        color: white;
        padding: 5px 10px;
        border: none;
        width: calc(100vw - 120px);
        padding-bottom: 2px;
        outline: none;
      }

      input:placeholder-shown {
        color: rgba(206, 204, 204, 0.664);
        font-weight: 400;
        font-size: 20px;
      }
    }

    .bigButton {
      display: block;
      text-align: center;
      padding: 15px 0px;
      text-decoration: none;
      font-size: 20px;
      font-weight: 400;
      width: 300px;
      border-radius: 30px;
      border: 0px;

      &:hover {
        box-shadow: 2px 2px 3px rgb(66, 3, 124);
      }
    }

    .selectedBtn {
      background: -webkit-linear-gradient(to right, #f3a73c, hwb(288 30% 11%));
      background: linear-gradient(to right, #f3a73c, hwb(288 30% 11%));
      color: white;
      border: none;
      cursor: pointer;

      &:hover {
        box-shadow: 2px 2px 3px rgb(66, 3, 124);
      }
    }

    .notSelectedBtn {
      color: rgb(92, 38, 187);
      background: none;
      background-color: white;
      font-size: 20px;
      font-weight: 400;
      border: none;
      cursor: pointer;

      &:hover {
        box-shadow: 2px 2px 3px rgb(66, 3, 124);
      }
    }
  }
}

